<template>
  <div>
    <div v-if="!showHistoryBool">
      <div v-if="!listIsEmptyComputed" class="row" id="history-row">
        <div id="admin-panel-container">
          <table>
            <thead>
              <tr>
                <th>Username</th>
                <th>Problem</th>
                <th>Solution</th>
                <th>Details and Conversation</th>
                <th>Approve</th>
              </tr>
            </thead>

            <tbody>
              <tr v-for="(data, index) in solutions" :key="index">
                <td class="username-td">{{ data.username }}</td>
                <td class="problem-td">
                  {{
                    data.problem.length > 30
                      ? data.problem.substring(0, 30) + "..."
                      : data.problem
                  }}
                </td>
                <td class="solution-td">
                  {{
                    data.solution.length > 30
                      ? data.solution.substring(0, 30) + "..."
                      : data.solution
                  }}
                </td>
                <td class="details-td">
                  <a
                    class="waves-effect waves-light btn btn-small"
                    @click="showHistory(index)"
                    :class="
                      data.conversation.items &&
                      data.conversation.items.length > 0
                        ? ''
                        : 'disabled'
                    "
                    >see details</a
                  >
                </td>
                <td class="approve-td">
                  <label>
                    <input
                      v-model="data.approved"
                      @click="
                        sendApproveUpdate(data.solution_id, !data.approved)
                      "
                      type="checkbox"
                    />
                    <span>{{
                      data.approved ? "Disapprove" : "&nbsp;Approve"
                    }}</span>
                  </label>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div v-else class="row" id="history-row">
        <div v-if="startDelay" class="col offset-s4 s4">
          <div class="card deep-purple darken-3">
            <div class="card-content white-text">
              <span class="card-title">There is no new solution yet ! </span>
              <p>
                Wait for user publication to see and edit new solutions !
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-else>
      <div id="admin-history-header">
        <h4>{{ currentUsername }}'s feedback conversation</h4>
      </div>

      <div class="admin-history-container">
        <div id="admin-conversation-linked-history">
          <div id="history-chat-panel">
            <ul class="collection with-header">
              <li class="collection-header">
                <button
                  class="waves waves-light btn deep-purple darken-3"
                  @click="showHistoryBool = false"
                >
                  Back
                </button>
              </li>
              <li
                id="history-chat-container"
                ref="messageContainerScroll"
                class="collection-item custom-grey "
              >
                <div>
                  <div
                    v-for="(message, index) in conversationComputed"
                    :key="index"
                  >
                    <chat-bot-message
                      v-if="message.author == 'chatbot'"
                      :content="message.content"
                    ></chat-bot-message>
                    <user-message
                      v-else
                      :author="message.author"
                      :content="message.content"
                    ></user-message>
                  </div>
                  <div>
                    <div v-if="true" class="card teal darken-1">
                      <div class="card-content white-text">
                        <span class="card-title"
                          >{{ currentUsername }} found this conversation
                          helpful</span
                        >
                      </div>
                    </div>
                    <div v-else class="card deep-purple darken-3">
                      <div class="card-content white-text">
                        <span class="card-title"
                          >{{ currentUsername }} found this conversation not
                          helpful</span
                        >
                        <p>
                          If you have found a solution to your problem please
                          contact us.
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div id="admin-history-header">
        <h4>Problems and solution in details</h4>
        <h5>{{ currentUsername }}'s problem</h5>
      </div>
      <div id="admin-history-content">
        <p>{{ currentProblem }}</p>
      </div>

      <div id="admin-history-header">
        <h5>{{ currentUsername }}'s solution</h5>
      </div>
      <div id="admin-history-header">
        <p>{{ currentSolution }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import UserMessage from "./components/UserMessage.vue";
import ChatBotMessage from "./components/ChatBotMessage.vue";
import axios from "axios";

export default {
  name: "Admin",
  components: {
    UserMessage,
    ChatBotMessage
  },
  data: function() {
    return {
      solutions: [],
      currentConversation: [],
      currentProblem: "",
      currentSolution: "",
      showHistoryBool: false,
      currentUsername: "",
      startDelay: false
    };
  },
  mounted() {
    axios
      .get("/admin-data")
      .then(response => {
        var data = response.data;
        if (data !== null && data.length > 0) {
          this.solutions = data;
        }
      })
      .catch(err => {
        alert("Error, no internet connexion.");
        console.trace(err);
      });

    setTimeout(() => {
      this.startDelay = true;
    }, 3000);
  },
  methods: {
    showHistory(index) {
      this.selectCurrentConversation(index);
      this.currentUsername = this.solutions[index].username;
      this.currentProblem = this.solutions[index].problem;
      this.currentSolution = this.solutions[index].solution;
      this.showHistoryBool = true;
    },
    selectCurrentConversation(index) {
      this.currentConversation = this.solutions[index].conversation.items;
    },
    async sendApproveUpdate(solutionId, approved) {
      axios
        .put(
          "/admin-data/solution",
          {
            solution_id: solutionId,
            approved: approved
          },
          {
            headers: {
              "Content-Type": "application/json"
            }
          }
        )
        .then(response => {})
        .catch(err => {
          alert("Error, no internet connexion.");
          console.trace(err);
        });
    }
  },
  computed: {
    listIsEmptyComputed() {
      return this.solutions.length === 0;
    },

    conversationComputed() {
      return this.currentConversation.map(el => {
        var toReturn;
        if (el.type === "Q") {
          toReturn = {
            author: this.currentUsername,
            content: el.text
          };
        } else {
          toReturn = {
            author: "chatbot",
            content: el.text
          };
        }
        return toReturn;
      });
    }
  }
};
</script>

<style lang="scss">
@import "/static/materialize/css/materialize.css";
@import "/static/css/chatbot.css";
@import "/static/css/history.css";
@import "/static/css/admin.css";
</style>
